import React from 'react';

export default function Enquiry() {
  return (
    <div>

{/* <!----------------========================================== start of enquiry_page ======================================----------------> */}
<div class="container-fluid enquiry_page">
    <div class="row mt30">
		<div class="col-md-6 col-sm-12 col-xs-12">
    		<h2>Enquiry <span>Form</span></h2>
    			<form class="form-horizontal mt30">
                    <div class="form-content">
                        <div class="form-group row">
                            <div class="col-sm-6 group">
                                <input class="form-control" name="name_of_company" placeholder="Name of company" type="text"/>
                            </div>
                            <div class="col-sm-6">
                                <input class="form-control" name="contact_person" placeholder="Contact Person" type="text"/>
                            </div>
                        </div>
                         <div class="form-group row">
                            <div class="col-sm-6">
                                <input class="form-control" name="address" placeholder="Postal Address" type="text"/>
                            </div>
                            <div class="col-sm-6">
                                <input class="form-control" name="city" placeholder="city" type="text"/>
                            </div>
                        </div>
                         <div class="form-group row">
                            <div class="col-sm-6">
                                <input class="form-control" name="phone" placeholder="Phone Number" type="text"/>
                            </div>
                            <div class="col-sm-6">
                                <input class="form-control" name="email" placeholder="Email" type="email"/>
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-6">
                                <select class="form-control">
                                    <option selected disabled>Select Package</option>
                                    <option value="1">Package Name1</option>
                                    <option value="2">Package Name2</option>
                                    <option value="3">Package Name3</option>
                                    <option value="4">Package Name4</option>
                                </select>
                            </div>
                            <div class="col-sm-6 form_back" >
                                <input class="form-control" name="coupon_code" placeholder="Enter Coupon Code" type="text"/>
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-12">
                                <textarea class="form-control" name="message" placeholder="Your Message"></textarea>
                            </div>
                        </div>
                        <div class="clearfix">
                            <a href="#" class="btn">Submit <i class="fa fa-angle-right"></i></a>
                        </div>
                    </div>
        		</form>
        </div>
    </div>
</div>
{/* <!----------------========================================== end of enquiry_page ======================================----------------> */}

    </div>
  )
}
