import React from 'react';

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import "@fancyapps/ui/src/Fancybox/Fancybox.js";
import "@fancyapps/ui/dist/fancybox.css";

import g1 from '../images/g1.jpg';
import g2 from '../images/g2.jpg';

import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';

import { useState } from 'react';

const options1 = {
    margin: 10,
    responsiveClass: true,
    nav: true,
    dots: false,
    autoplay: true,
    loop: true,
    navText: false,
    smartSpeed: 1000,
    responsive: {
        0: {items: 1,},
        400: {items: 1,},
        600: {items: 1,},
        700: {items: 1,},
        1000: {items: 1,}
    },
};


export default function ProductDetail() {
    const [count, setCount] = useState(0);

    function increment() {
      setCount(function (prevCount) {
        return (prevCount += 1);
      });
    }
  
    function decrement() {
      setCount(function (prevCount) {
        if (prevCount > 0) {
          return (prevCount -= 1); 
        } else {
          return (prevCount = 0);
        }
      });
    }

  return (
    <div>

{/* <!----------------========================================== start of product_inner_page ======================================----------------> */}
<div class="container-fluid product_inner_page">
    <div class="row mt30">
		<div class="col-md-12 col-sm-12 col-xs-12">
    		<h2>Products <span>We Deal In</span></h2>
        </div>
    </div>
    <div class="row">
		<div class="col-md-4 col-sm-12 col-xs-12 mt30">
            <OwlCarousel className="slider-items owl-one owl-theme owl-carousel" {...options1}>
              <div class='item'>
                <a href="#" data-fancybox="gallery" data-src={g1} class="image"><img class="pic-1" src={g1} /></a>
              </div>
              <div class='item'>
                <a href="#" data-fancybox="gallery" data-src={g2} class="image"><img class="pic-1" src={g2} /></a>
              </div>
            </OwlCarousel>
        </div>
        <div class="col-md-5 col-sm-12 col-xs-12 mt30">
            <div class="main_title">Ohio Semitronics Cta-1248 Transducer</div>
            <div class="row">
                <div class="col-md-4 col-sm-12 col-xs-12">
                    <div class="price">$62.99</div>
                </div>
                <div class="col-md-8 col-sm-12 col-xs-12">
                    <div class="rating pull-right">BE THE FIRST TO REVIEW THIS PRODUCT</div>
                </div>
            </div>
            <div class="stock"><span>In stock</span></div>
            <div class="row">
                <div class='col-md-3 col-sm-12 col-xs-12 mt30'>
                    <div class="qty-input">
                        <button class="qty-count qty-count--minus" type="button" onClick={decrement}>-</button>
                        <h1 class="product-qty">{count}</h1>
                        <button class="qty-count qty-count--add" type="button" onClick={increment}>+</button>
                    </div>
                </div>
            </div>
            <div class="row brdr mt30">
                <div class="col-md-5 col-sm-12 col-xs-12 mt15">
                    <a href='#'><div class="btn">Add To Cart</div></a>
                </div>
                <div class="col-md-4 col-sm-12 col-xs-12 mt15">
                    <a href='#'><div class="btn1">Buy Now</div></a>
                </div>
                <div class="col-md-3 col-sm-6 col-xs-6 text-center mt15">
                    <ul class="btn_link">
                        <li><div class='action1'><i class="fa fa-heart"></i></div></li>
                        <li><div class='action2'><i class="fa fa-exchange"></i></div></li>
                    </ul>
                </div>
            </div>
            <div class="row mt30">
                <p><strong>Condition:</strong>  Used / Second hand / Excellent Condition</p>
                <div class="row">
                    <div class="col-md-7 col-sm-12 col-xs-12">
                        <p><strong>Quick Overview</strong></p>
                    </div>
                    <div class="col-md-5 col-sm-12 col-xs-12">
                        <ul class="social pull-right">
                            <li><a href="#"><i class="fa fa-facebook wc"></i></a></li>
                            <li><a href="#"><i class="fa fa-twitter wc"></i></a></li>
                            <li><a href="#"><i class="fa fa-linkedin wc"></i></a></li>
                            <li><a href="#"><i class="fa fa-pinterest wc"></i></a></li>
                        </ul>
                    </div>
                </div>
                <ul>
                    <li>Ohio semitronics cta-1248 transducer</li>
                    <li>ohio semitronics,inc.</li>
                    <li>model:cta-1248</li>
                    <li>transducer</li>
                    <li>input:0-50mvdc</li>
                    <li>output:0-4ma/10vdc</li>
                </ul>
            </div>
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12">
            <div class="row">
                <div class="col-md-12 col-sm-12 col-xs-12 mt15">
                    <div class="box">
                        <div class="row">
                            <div class="col-md-4 col-sm-12 col-xs-12">
                                <div class="icon"><i class="fa fa-truck"></i></div>
                            </div>
                            <div class="col-md-8 col-sm-12 col-xs-12">
                                <div class='service_list'>Free Shipping On Orders Over $100</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 col-sm-12 col-xs-12 mt15">
                    <div class="box">
                        <div class="row">
                            <div class="col-md-4 col-sm-12 col-xs-12">
                                <div class="icon"><i class="fa fa-cc-visa"></i></div>
                            </div>
                            <div class="col-md-8 col-sm-12 col-xs-12">
                                <div class='service_list'>Accept payments online</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 col-sm-12 col-xs-12 mt15">
                    <div class="box">
                        <div class="row">
                            <div class="col-md-4 col-sm-12 col-xs-12">
                                <div class="icon"><i class="fa fa-money"></i></div>
                            </div>
                            <div class="col-md-8 col-sm-12 col-xs-12">
                                <div class='service_list'>Money back guarantee</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 col-sm-12 col-xs-12 mt15">
                    <div class="box">
                        <div class="row">
                            <div class="col-md-4 col-sm-12 col-xs-12">
                                <div class="icon"><i class="fa fa-check"></i></div>
                            </div>
                            <div class="col-md-8 col-sm-12 col-xs-12">
                                <div class='service_list'>Ships items quickly</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="mt15">&nbsp;</div>
    <div class="row mt30">
        <Tabs defaultActiveKey="home" id="uncontrolled-tab-example" className="mb-3">
            <Tab eventKey="home" title="PRODUCT DETAILS">
                <ul>
                    <li>Yokogawa eml500-hd1 magnetic compass</li>
                    <li>yokogawa denshikiki co.,ltd.</li>
                    <li>eml500-hd1</li>
                    <li>type approval no.3795</li>
                    <li>model:lt501 w</li>
                    <li>mass:5.0kg</li>
                    <li>standard magnetic compass</li>
                    <li>Made in INDIA</li>
                </ul>
            </Tab>
            <Tab eventKey="profile" title="BRAND DETAILS">
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                <p>Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
            </Tab>
            <Tab eventKey="contact" title="MORE FROM THIS BRAND (0)">
                <p>Products from the same brand</p>
                <div class="message info empty"><div>We can't find products matching the selection.</div></div>
            </Tab>
        </Tabs>
    </div>
</div>
{/* <!----------------========================================== end of product_inner_page ======================================----------------> */}

    </div>
  )
}